.container {
  padding: 20px;
  width: 100%;
  max-width: 250px;
  background-color: white;
  box-shadow:
    rgba(17, 17, 26, 0.05) 0px 1px 0px,
    rgba(17, 17, 26, 0.1) 0px 0px 8px;
  margin: 20px 20px 0 40px;
  height: 540px;
}
@media screen and (max-width: 829px) {
  /* 모바일 스타일 */
  .container {
    display: none;
    /* width: 100%;
    max-width: 800px; */
  }
}
.category_container {
  /* margin-top: 20px; */
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
}
.category_container > div {
  cursor: pointer;
  padding: 10px;
  display: flex;
  align-items: center;
}
.category_container > div > div:last-child {
  margin-left: 10px;
}
.showPeoples {
  cursor: pointer;
}
.showPeoples_container > div {
  font-size: 15px;
  cursor: pointer;
}
.category_container div:not(:last-child) {
  border-bottom: 1px solid rgba(112, 112, 112, 0.2);
}
